*
{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}
body, #root
{
  width: 100vw;
  height: 100vh;
}
.gpx-submit
{
  background-color: rgb(196, 206, 109);
  border-style: solid;
  border-color: black;
  border-radius: 50px;
  height: 20%;
  width: 50%;
  position: absolute;
  top: 50%;
  cursor: pointer;
  transition: .3s;
}
.gpx-submit:hover
{
  background: rgb(199, 192, 192);
}
.gpx-form input[type="text"]
{
  height: clamp(20px, 30%, 50px);
  width: 70%;
  border-radius: 20px;
  border-style: solid;
  position: absolute;
}
.gpx-form
{
  width: clamp(200px, 50vw, 300px);
  height: clamp(150px, 30vh, 300px);
  border-style: solid;
  border-radius: 20px;
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50vw;
  top: 20vh;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
}
